<template>

    <section class="toolbox-wrap">
        <div class="toolbox-inner">

            <h5 class="toolbox-title">年份樣式</h5>

            <div class="form-group">
                <label for="year-show">顯示文字</label>
                <input type="checkbox" id="year-show" name="year-show" 
                    :checked="initData.year_display" 
                    @change="changeYearDisplay">
            </div>

            <div class="form-group">
                <label for="year-text">文字</label>
                <input id="year-text" name="year-text" type="text" 
                    v-model="initData.year_text" 
                    @change="changeYearText"
                    @keyup="changeYearText">
            </div>

            <div class="form-group">
                <label for="year-color">顏色</label>
                <input id="year-color" name="year-color" type="color" 
                    v-model="initData.year_color" 
                    @change="changeYearColor"
                    @keyup="changeYearColor">
            </div>

            <div class="form-group">
                <label for="year-font-size">字體大小</label>
                <input name="year-font-size" min="12" max="100" type="number" 
                    v-model="initData.year_font_size" 
                    @change="changeYearSize"
                    @keyup="changeYearSize">px
            </div>

            <div class="form-group">
                <label for="year-font-weight">字重</label>
                <select name="year-font-weight" id="year-font-weight" 
                    v-model="initData.year_font_weight" 
                    @change="changeYearWeight">

                    <option value="normal">normal</option>
                    <option value="bold">bold</option>
                    <option value="bolder">bolder</option>
                    <option value="lighter">lighter</option>
                    <option value="100">100</option>
                    <option value="200">200</option>
                    <option value="300">300</option>
                    <option value="400">400</option>
                    <option value="500">500</option>
                    <option value="600">600</option>
                    <option value="700">700</option>
                    <option value="800">800</option>
                    <option value="900">900</option>
                </select>
            </div>

            <div class="form-group">
                <label for="year-font-family-link">Google 字型 link</label>
                <textarea id="year-font-family-link" name="year-font-family-link" cols="40" rows="3" 
                    v-model="initData.year_font_family_link" 
                    @keydown.enter.exact.prevent 
                    @keyup.enter.exact="setFontLink"
                    @keyup="setFontLink"></textarea>
            </div>

            <div class="form-group">
                <label for="year-font-family-name">Google 字型 CSS 名稱</label>
                <input id="year-font-family-name" name="year-font-family-name" type="text" 
                    v-model="initData.year_font_family_name" 
                    @change="changeYearFont"
                    @keyup="changeYearFont">
            </div>


            <h5 class="toolbox-title">輸出</h5>

            <div class="form-group">
                <label for="bg-color">背景顏色</label>
                <input type="color" id="bg-color" name="bg-color"
                    v-model="initData.background_color" 
                    @change="changeBackgroundColor">
            </div>

            <div class="form-group">
                <label for="output-size">輸出尺寸 (正方形)</label>
                <input type="number" id="output-size" name="output-size"
                    v-model="initData.output_size" 
                    @change="changeOutputSize">
            </div>

            <div class="form-group">
                <button id="takeshot-btn" @click="getScreenshot">產生圖檔</button>
            </div>

        </div>
        
    </section>

</template>

<script>
import { toRef } from 'vue';

export default {
    name: 'SixteenGriderToolbox',
    props: ['data'],
    emits: [
        'eventChangeYearDisplay',
        'eventChangeYearText',
        'eventChangeYearColor',
        'eventChangeYearWeight',
        'eventChangeYearFont',
        'eventSetFontLink',
        'eventGetScreenshot',
        'eventChangeBackgroundColor',
        'eventChangeOutputSize'
    ],

    setup(props, context) {
        // 從 props 來的數據
        const initData = toRef(props, 'data');

        // 數據
        


        // 方法

        function changeYearDisplay() {
            context.emit('eventChangeYearDisplay', !initData.value.year_display);
        }

        function changeYearText() {
            context.emit('eventChangeYearText', initData.value.year_text);
        }

        function changeYearColor() {
            context.emit('eventChangeYearColor', initData.value.year_color);
        }

        function changeYearWeight() {
            context.emit('eventChangeYearWeight', initData.value.year_font_weight);
        }

        function changeYearFont() {
            context.emit('eventChangeYearFont', initData.value.year_font_family_name);
        }


        // 設定匯入的字型
        function setFontLink() {
            context.emit('eventSetFontLink', initData.value.year_font_family_link)
        }



        // 修改背景色
        function changeBackgroundColor() {
            context.emit('eventChangeBackgroundColor', initData.value.background_color)
        }

        // 修改輸出尺寸
        function changeOutputSize() {
            context.emit('eventChangeOutputSize', initData.value.output_size)
        }


        function getScreenshot() {
            context.emit('eventGetScreenshot');
        }




        //---------------------------------------------
        // 將 rgb(red, green ,blue) 字串 轉換為 JSON物件
        // 來源: https://stackoverflow.com/questions/34980574/how-to-extract-color-values-from-rgb-string-in-javascript
        //---------------------------------------------
        // function getRGB(str) {
        //     var match = str.match(/rgba?\((\d{1,3}), ?(\d{1,3}), ?(\d{1,3})\)?(?:, ?(\d(?:\.\d?))\))?/);
        //     return match ? {
        //     red: parseInt(match[1]),
        //     green: parseInt(match[2]),
        //     blue: parseInt(match[3])
        //     } : {};
        // }

        //-----------------------
        // 將 0~255 轉換為 16進位
        // 來源: https://stackoverflow.com/questions/5623838/rgb-to-hex-and-hex-to-rgb
        //-----------------------
        // function componentToHex(c) {
        //     var hex = c.toString(16);
        //     return hex.length == 1 ? "0" + hex : hex;
        // }
        
        //-----------------------
        // 將 rgb 轉換為 hex
        // 來源: https://stackoverflow.com/questions/5623838/rgb-to-hex-and-hex-to-rgb
        //-----------------------
        // function rgbToHex(r, g, b) {
        //     return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
        // }


        return {
            initData,
            
            changeYearDisplay,
            changeYearText,
            changeYearColor,
            changeYearWeight,
            changeYearFont,
            setFontLink,
            changeBackgroundColor,
            changeOutputSize,

            getScreenshot,
        }
    }
}
</script>