<template>

    <div class="sixteen-grider">

        <!-- Body -->
        <SixteenGriderBody
            ref="bodyRef"
            :data="data"
        />

        <!-- Toolbox -->
        <SixteenGriderToolbox 
            :data="data"
            @eventChangeYearDisplay="changeYearDisplay"
            @eventChangeYearText="changeYearText"
            @eventChangeYearColor="changeYearColor"
            @eventChangeYearWeight="changeYearWeight"
            @eventChangeYearFont="changeYearFont"
            @eventSetFontLink="setFontLink"
            @eventChangeBackgroundColor="changeBackgroundColor"
            @eventChangeOutputSize="changeOutputSize"
            @eventGetScreenshot="getScreenshot"
        />

        <!-- Output -->
        <SixteenGriderOutput
            ref="outputRef"
            v-show="isShowOutput"
            @eventClosedModal="closedModal"
        />

    </div>

</template>

<script>
// 引入 html2canvas
import html2canvas from 'html2canvas';

import { ref, reactive, onMounted } from 'vue';

// components
import SixteenGriderToolbox from './SixteenGriderToolbox.vue';
import SixteenGriderOutput from './SixteenGriderOutput.vue';
import SixteenGriderBody from './SixteenGriderBody.vue';

export default {
    name: 'SixteenGrider',
    components: {
        SixteenGriderToolbox,
        SixteenGriderOutput,
        SixteenGriderBody,
    },

    setup() {
        // 數據
        const data = reactive({
            count_x: 4,
            count_y: 4,

            year_text: '',
            year_display: true,

            year_color: '#000000',
            year_font_size: 35,
            year_font_weight: '400',

            // 字體
            year_font_family_link: 'https://fonts.googleapis.com/css2?family=Vast+Shadow&display=swap',
            year_font_family_name: 'Vast Shadow',

            // 背景色
            background_color: '#FFFFFF',

            // 輸出尺寸
            output_size: 1024,
        })



        let isShowOutput = ref(false); // 是否顯示 SixteenGriderOutput 子組件


        // 元素
        let bodyRef = ref(null);  // SixteenGriderBody 子組件
        let outputRef = ref(null);  // SixteenGriderOutput 子組件
        


        // 方法

        function changeYearDisplay(value) {
            data.year_display = value;
        }

        function changeYearText(value) {
            data.year_text = value;
        }

        function changeYearColor(value) {
            data.year_color = value;
        }

        function changeYearWeight(value) {
            data.year_font_weight = value;
        }

        function changeYearFont(value) {
            data.year_font_family_name = value;
        }

        function changeBackgroundColor(value) {
            data.background_color = value;
        }

        function changeOutputSize(value) {
            data.output_size = value;
        }



        // 設定匯入的字型
        function setFontLink(url) {

            // 如果有 url 傳來
            if ( url ) {
                data.year_font_family_link = url;  // 更新資料
            }

            // 設定 head 匯入的字型 link
            document.querySelector('link[href*="fonts.googleapis"]').href = data.year_font_family_link;
        }


        // 產生圖檔
        function getScreenshot() {
            isShowOutput.value = true;
            // $(document).scrollTop(0);

            // 文字縮放率 (根據欲輸出的尺寸決定文字縮放率)
            let text_ratio = data.output_size / bodyRef.value.captureRef.offsetWidth;
            bodyRef.value.yearRef.style.transform = `scale(${Math.floor(text_ratio * 100) / 100})`;
            
            // 修改容器尺寸
            bodyRef.value.captureRef.style.width = `${data.output_size}px`;
            bodyRef.value.captureRef.style.height = `${data.output_size}px`;



            // 清空內部元素
            outputRef.value.imgWrapRef.replaceChildren();  // 放置產出的canvas圖片
    
            // 使用 html2canvas 將元素轉換成 canvas圖片
            html2canvas( bodyRef.value.captureRef ).then(canvas => {
                // 將canvas圖片插入畫面
                outputRef.value.imgWrapRef.append(canvas);

                // 還原數值
                bodyRef.value.captureRef.style.width = '';
                bodyRef.value.captureRef.style.height = '';
                bodyRef.value.yearRef.style.transform = '';
            });

            // html2canvas( bodyRef.value.captureRef, {
            //     width: 1024,
            //     height: 1024,
            // } ).then(canvas => {
            //     // 將canvas圖片插入畫面
            //     outputRef.value.imgWrapRef.append(canvas);
            // });

        }

        // 關閉 modal
        function closedModal() {
            isShowOutput.value = false;
        }




        onMounted(()=>{
            // 設定預設值
            let today = new Date();
            data.year_text = today.getFullYear();

            // 設定匯入的字型
            setFontLink();
        })


        return {
            data,
            isShowOutput,

            bodyRef,
            outputRef,

            changeYearDisplay,
            changeYearText,
            changeYearColor,
            changeYearWeight,
            changeYearFont,
            setFontLink,
            changeBackgroundColor,
            changeOutputSize,

            getScreenshot,
            closedModal,
        }
    }
}
</script>