<template>

    <section class="output-wrap">
        <div class="colsed-btn" @click="closedModal"></div>

        <!-- 放置產生後的圖檔 -->
        <div ref="imgWrapRef"></div>
    </section>

</template>
<script>
import { ref } from 'vue';


export default {
    name: 'SixteenGriderOutput',
    emits: [
        'eventClosedModal',
    ],

    setup(props, context) {

        // 元素
        let imgWrapRef = ref(null);


        // 方法
        

        // 關閉 modal
        function closedModal() {
            context.emit('eventClosedModal');
        }


        return {
            imgWrapRef,

            closedModal,
        }
    }
}
</script>